* {
    box-sizing: border-box;
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    text-align: center;
    /*color: #001C30; */
}


/* The switch - the box around the slider */

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}


/* Hide default HTML checkbox */

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}


/* The slider */

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 40px;
    width: 40px;
    left: 0px;
    bottom: 4px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    box-shadow: 0 0px 15px #2020203d;
    background: white url('https://i.ibb.co/FxzBYR9/night.png');
    background-repeat: no-repeat;
    background-position: center;
}

input:checked+.slider {
    background-color: #2196f3;
}

input:focus+.slider {
    box-shadow: 0 0 1px #2196f3;
}

input:checked+.slider:before {
    -webkit-transform: translateX(24px);
    -ms-transform: translateX(24px);
    transform: translateX(24px);
    background: white url('https://i.ibb.co/7JfqXxB/sunny.png');
    background-repeat: no-repeat;
    background-position: center;
}


/* Rounded sliders */

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

body {
    color: #001C30;
}

body.night-mode {
    background-color: #161616;
    color: #fff;
}

.mobile-br {
    display: none;
}

ul {
    list-style: none;
}

header {
    padding: 2.25vh 0;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #000;
    transform: translateY(0);
    top: 0;
    transition: transform .3s;
}

header.night-mode {
    background-color: #232323;
}

header.scrolling-down {
    transform: translateY(-100%);
}

header h1 {
    color: #fff;
    text-transform: uppercase;
}

header h1.night-mode {
    color: #000;
    color: #fff;
}

.add__product__button {
    background-color: #fff;
    border-radius: 4px;
    border-style: none;
    box-sizing: border-box;
    color: #001C30;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.5;
    margin: 0;
    max-width: none;
    min-height: 44px;
    min-width: 10px;
    outline: none;
    overflow: hidden;
    padding: 9px 20px 8px;
    position: relative;
    text-align: center;
    user-select: none;
    touch-action: manipulation;
    width: auto;
}

.add__product__button.night-mode {
    background-color: #121212;
    color: #fff;
}

.add__product__button:hover,
.add__product__button:focus {
    opacity: .75;
}

.items__list {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding-top: 15vh;
    padding-bottom: 0.25rem;
    border-bottom: 2px solid #001C30;
    font-weight: 600;
}


/* SELECT VIEW */

.select-view-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: auto;
    margin-left: auto;
    margin-right: 4rem;
}

.select-view-options .select-option {
    font-size: 1.55rem;
    cursor: pointer;
}

.select-view-options .select-option:first-of-type {
    margin-right: 1rem;
    font-size: 1.9rem;
}

.carts__view {
    border-bottom: none;
}

.products__section {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: 97.5%;
    margin: 5vh auto;
}

.items__grid {
    overflow: hidden;
    box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
    width: 27.5%;
    margin: 2.5vh 2%;
    display: flex;
    min-height: 275px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.items__grid.night__mode {
    box-shadow: none;
    background-color: #262626;
    border-radius: 15px;
}

.items__grid .items__list__row__position {
    color: #000;
}

.item__name {
    font-size: 1.7rem;
    text-transform: uppercase;
    order: 1;
}

.item__date {
    font-size: 1.25rem;
    order: 2;
}

.item__date .days__until {
    font-size: 1.15rem;
}

.item__remove {
    order: 4;
}

.item__category {
    text-transform: uppercase;
    font-variant: all-small-caps;
    font-size: 1.5rem;
    order: 1;
}

.items__list__row,
.categories__list__row {
    font-size: 1.4rem;
    margin: 0 auto;
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    align-items: center;
}

.items__list__row {
    margin-top: 4vh;
}

.items__list__row__position,
.category__list__row__position {
    width: 25%;
}

.remove__item {
    background: #FF4742;
    border: 1px solid #FF4742;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    display: inline-block;
    font-family: nunito, roboto, proxima-nova, "proxima nova", sans-serif;
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
    min-height: 36px;
    outline: none;
    padding: 7px 12px;
    text-align: center;
    text-rendering: geometricprecision;
    text-transform: none;
    user-select: none;
    touch-action: manipulation;
    vertical-align: middle;
    transition: .2s;
}

.remove__item:hover,
.remove__item:active {
    background-color: transparent;
    background-position: 0 0;
    color: #FF4742;
}

.remove__item:active {
    opacity: .5;
    transition: 0;
}

.days__until {
    font-size: 0.88rem;
}

.days__until span {
    font-weight: bold;
}


/*  MODAL  */

.modal {
    display: none;
    position: fixed;
    z-index: 25;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.7);
    overflow: hidden;
}

.modal__content {
    background-color: #001C30;
    padding: 25px 30px;
    border: none;
    width: 75%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.modal__content .close__modal {
    color: #dcdcdc;
    display: flex;
    margin-left: auto;
    font-size: 34px;
    transition: .15s;
}

.close__modal:hover,
.close__modal:focus {
    color: red;
    text-decoration: none;
    cursor: pointer;
}

.modal.show__modal {
    display: block;
}

.modal__row {
    display: flex;
    justify-content: space-evenly;
    width: 80%;
    margin: 5% auto 7.5%;
}

.modal label {
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.4rem;
    text-transform: uppercase;
    letter-spacing: 1.25px;
    word-spacing: 10px;
    width: 30%;
}

.modal label input,
#category {
    margin-top: 5%;
    box-sizing: border-box;
    font-family: inherit;
    font-size: 17px;
    vertical-align: baseline;
    font-weight: 400;
    line-height: 1.3;
    letter-spacing: .16px;
    border-radius: 0;
    outline: 2px solid transparent;
    outline-offset: -2px;
    width: 100%;
    height: 50px;
    border: none;
    border-bottom: 1px solid #8d8d8d;
    background-color: #f4f4f4;
    padding: 0 16px;
    color: #161616;
    transition: background-color 70ms cubic-bezier(.2, 0, .38, .9), outline 70ms cubic-bezier(.2, 0, .38, .9);
}

#category {
    padding: 0;
    margin-left: 16px;
    margin-right: 16px;
}

select {
    -webkit-appearance: button !important;
    -moz-appearance: button !important;
    appearance: button !important;
    cursor: pointer;
}

#category option {
    text-transform: lowercase;
}

.modal label input:focus {
    outline: 2px solid #001C30;
    outline-offset: -2px;
}

.modal label input:focus::placeholder {
    color: transparent;
    transition: .125s ease;
}

.modal button {
    display: inline-block;
    outline: 0;
    border: 0;
    cursor: pointer;
    background: #fff;
    color: #000;
    border-radius: 8px;
    padding: 14px 24px 16px;
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
    transition: transform 150ms, background 150ms;
}

.modal button:hover {
    transform: translateY(-2px);
}

.expiration-date {
    margin: 0 auto;
    width: fit-content;
    cursor: pointer;
}

.expiration-date__icon {
    font-size: 1.15rem;
}

@media screen and (max-width:767px) {
    .items__list__row,
    .categories__list__row {
        width: 95%
    }
    .items__list__row__position {
        font-size: 1.05rem;
    }
    .remove__item {
        width: 30px;
        min-height: 30px;
        line-height: normal;
        padding: 0;
    }
    .items__list__row__position {
        width: 30%;
    }
    .items__list__row__position:last-of-type {
        width: 10%;
    }
    .category__list__row__position {
        font-size: 1rem;
    }
    .mobile-br {
        display: flex;
    }
}